*{
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
        Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    background-color: #eff3f7;
    height: 100%;
}

html,
body,
.container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 1000;
}

.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 50px;
    width: 90%;
}

.movies .movie {
    width: 45%;
    background-color: rgb(245, 235, 235);
    margin-bottom: 80px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 400;
    padding: 20px;
    border-radius: 30px;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img {
    position: relative;
    top: -30px;
    max-width: 110px;
    margin-right: 15px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
        0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie__title,
.movie .movie__year,
.movie .movie__rating {
    margin: 0;
    font-weight: 500;
}

.movie .movie__title {
    margin-bottom: 5px;
    font-size: 23px;
    color: #2c2c2c;
}

.movie .movie__genres {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    margin: 2px 0px;
}

.movie__genres li {
    margin-right: 10px;
}

